<template>
  <div class="bank-card-page">
      <Header>
        <span slot="right" @click="catbank">支持银行</span>
      </Header>
      <!-- <van-nav-bar
        title="提现管理"
        left-text=""
        right-text="支持银行"
        left-arrow
        @click-left="onClickLeft"
        @click-right="catbank"
      /> -->
      <!-- 等待状态 -->
      <div class="loading" v-if="show">
        <van-loading color="#1989fa" size="48px" vertical>加载中...</van-loading>
      </div>
      <div class="card-lists" v-else>
        <div class="card-item" v-if="!isBindYHK">
          <template v-if="!([2,3].includes(identifyNo))">
            <a href="javascript:;" @click="doAddCard"><i></i>添加银行卡</a>
          </template>
        </div>
        <div class="card-item-preview" v-else :style="{'background-color': getBankStyleInfo[bankInfo.bankNo] ? getBankStyleInfo[bankInfo.bankNo].background : '#39A8FF'}" @click="goCardDetail(bankInfo.cardNoShow)">
          <div class="card-preview-left">
            <img v-if="getBankStyleInfo[bankInfo.bankNo]" :src="'//cdn09.ehaier.com/shunguang/H5/www/img/'+getBankStyleInfo[bankInfo.bankNo].img+'.png'" alt="">
          </div>
          <div class="card-preview-right">
            <div>{{bankInfo.bankName}} <img src="//cdn09.ehaier.com/shunguang/H5/www/img/baozhang.png" :alt="bankInfo.bankName"></div>
            <p>{{bankInfo.cardNo}}</p>
          </div>
          <img v-if="getBankStyleInfo[bankInfo.bankNo]" class="card-preview-bg" :src="'//cdn09.ehaier.com/shunguang/H5/www/img/'+getBankStyleInfo[bankInfo.bankNo].img+'.png'" alt="">
        </div>
        <div class="card-item" v-if="!isBindKJT && identify" @click="doAddKjt">
          <a href="javascript:;"><i></i>绑定快捷通</a>
        </div>
        <div class="card-item-preview" v-if="isBindKJT" :style="{'background-color': getBankStyleInfo['88888888'].background}" @click="toRelieve">
          <div class="card-preview-left">
            <div><img :src="'//cdn09.ehaier.com/shunguang/H5/www/img/'+getBankStyleInfo['88888888'].img+'.png'" alt=""></div>
          </div>
          <div class="card-preview-right">
            <div>快捷通 <img src="//cdn09.ehaier.com/shunguang/H5/www/img/baozhang.png" alt="快捷通"></div>
            <p v-if="kjtInfo">{{kjtInfo.memberKjtpayAccount}}</p>
          </div>
          <img class="card-preview-bg" :src="'//cdn09.ehaier.com/shunguang/H5/www/img/'+getBankStyleInfo['88888888'].img+'.png'" alt="">
        </div>
      </div>
      <!-- 提示 -->
      <div class="hint" v-if="showTip">
        <img :src="`${$cdnImageUrl}/login/pwd.png`" alt="">温馨提示：{{title}}
      </div>
  </div>
</template>

<script>
import util from '@/utils/utils'
import Header from '@/components/header'
import URL from '@/http/url'
import { mapGetters } from 'vuex'

export default {
  name: 'bankcard',
  data() {
    return {
      isBindKJT: false,
      identify: true,
      identifyNo: '',
      isBindYHK: false,
      bankInfo: null,
      kjtInfo: null,
      title: '鉴于获取到您的企业性质为空，请核对您提供的企业信息是否正确，如有误，请及时联系中心更改建店信息。',
      showTip: false,
      show: true,
    }
  },
  computed: {
    ...mapGetters('bankcard', ['getBankStyleInfo']),
    ...mapGetters(['getSessionValue'])
  },
  components: {
    Header,
  },
  activated() {
    this.fetchInit()
    console.log(this.getBankStyleInfo);
  },
  methods: {
    onClickLeft() {
      this.$router.push('/mine')
    },
    catbank() {
      this.$router.push('/bankslist')
    },
    doAddCard() {
      this.$router.push({ path: '/bindcard', query: { identifyFlag: this.identifyNo } })
    },
    doAddKjt() {
      this.$http.get(URL.KJT_IS_REALNAMEAUTH).then(res => {
        let result = res.data;
        if (result.success) {
          if ((result.data.authCode & 1) === 1 || (result.data.authCode & 4) === 4) {
            util.openNewWindow(URL.KJT_BIND_INIT + '?flag=' + encodeURIComponent(this.getSessionValue))
          } else {
            this.$toast('请核对营业执照信息是否认证通过')
          }
        }
      })
    },
    fetchInit() {
      this.$http.get(URL.HAS_BANK_LIST_NEW).then(({ data }) => {
        this.show = false
        if (data.success && data.data) {
          let result = data.data;
          this.isBindYHK = result.isBindYHK
          this.isBindKJT = result.isBindKjt
          this.bankInfo = result.BankCardList
          this.kjtInfo = result.kjtAccount
          this.title = result.showMessage
          this.identifyNo = result.identify
          if (result.identify !== 0 && result.identify) {
            this.showTip = true
          } else {
            this.showTip = false
          }
          if (result.identify === 3 || null) {
            this.identify = false
          }
        } else {
          this.$toast(data.message || '接口异常,未返回异常信息')
        }
      }).catch(errResult => {
        this.$toast('网络请求失败')
      })
    },
    goCardDetail(cardNo) {
      this.$router.push({
        name: 'unbindcard',
        params: { cardNo }
      })
    },
    toRelieve() {
      this.$router.push({ path: '/relieve', query: { memberKjtpayAccount: this.kjtInfo.memberKjtpayAccount } })
    }
  }
}
</script>

